import {animate, state, style, transition, trigger} from "@angular/animations";

/***********************************************************
 **  @project
 **  @file
 **  @author Brice Daupiard <brice.daupiard@nowbrains.com>
 **  @Date 09/08/2022
 **  @Description
 ***********************************************************/
export const FadeInOut = trigger('fadeInOut', [
  state(
    'loaded',
    style({
      opacity: 0,
      'z-index': -1
    })
  ),
  state(
    'loading',
    style({
      opacity: 1,
    })
  ),
  transition('* => *', animate('500ms ease-out')),
])
