import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class AnimationsService {

  private status = true;
  constructor() {
  }


  FadeSlideInOut(ttx: number, offset_percent: number, value: boolean = true, instant: boolean = false) {

    const status = value ? ':enter' : ':leave'
    if (status === ':leave') {
      return {
        value: status,
        params: {
          ttx: 0,
          percent_start: 0,
          percent_start_1: 0,
          percent_start_2: 0
        }
      }
    } else return {
      value: status,
      params: {
        ttx,
        percent_start: offset_percent,
        percent_start_1: offset_percent - (offset_percent * 0.3),
        percent_start_2: offset_percent - (offset_percent * 0.6)
      }
    }


  }

  FadeSlideInOutKonverso(ttx: number, offset_percent: number, value: boolean = true, instant: boolean = false) {

    const status = value ? ':enter' : ':leave'
    if (status === ':leave') {
      return {
        value: status,
        params: {
          ttx: 0,
          percent_start: 0,
          percent_start_1: 0,
          percent_start_2: 0
        }
      }
    } else return {
      value: status,
      params: {
        ttx,
        percent_start: offset_percent,
        percent_start_1: offset_percent - (offset_percent * 0.3),
        percent_start_2: offset_percent - (offset_percent * 0.6)
      }
    }

  }

  FadeSlideInOutCasesFeedHome(ttx: number, offset_percent: number, value: boolean = true, instant: boolean = false) {

    const status = value ? ':enter' : ':leave'
    if (status === ':leave') {
      return {
        value: status,
        params: {
          ttx: ttx,
          percent_start: 0,
          percent_start_1: 0,
          percent_start_2: 0
        }
      }
    } else return {
      value: status,
      params: {
        ttx,
        percent_start: offset_percent,
        percent_start_1: offset_percent - (offset_percent * 0.3),
        percent_start_2: offset_percent - (offset_percent * 0.6)
      }
    }

  }


  FadeSlideInOutStagger(ttx: number, offset_percent: number, value: boolean = true, stagger: number = 10) {
    const status = value ? ':enter' : ':leave'
    if (status === ':leave') {

      return {
        value: status,
        params: {
          ttx: ttx / 2,
          percent_start: offset_percent - (offset_percent * 0.6),
          percent_start_1: offset_percent - (offset_percent * 0.3),
          percent_start_2: offset_percent,
          stagger : 10
        }
      }
    } else return {
      value: status,
      params: {
        ttx,
        percent_start: offset_percent,
        percent_start_1: offset_percent - (offset_percent * 0.3),
        percent_start_2: offset_percent - (offset_percent * 0.6),
        stagger
      }
    }
  }

}
